<template>
  <div id="list-events" class="row justify-content-md-center mt-5 mb-5">
    <div class="col-sm-6">
      <h1>List All events</h1>
      <div class="mb-3">
        <router-link 
          to="/events/new" 
          class="btn btn-secondary ml-1">
          <i class="fa fa-plus"></i>
        </router-link>
      </div>
      <div class="list-group">
        <router-link 
          v-for="event in events" 
          v-bind:key="event.id" 
          v-bind:to="event.id" 
          class="list-group-item list-group-item-action"
          >
          <div v-if="event.state" class="badge badge-secondary">
            {{ event.state }}
          </div>
          &nbsp;
          {{ event.name }}
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import db from '../lib/firebaseInit'
export default {
  name:'list-events'
  , data() {
    return {
      updatedAt:''
      , createdAt: ''
      , events: []
      , state: ''
    }
  }, 
  created() {
    db.collection('events')
    .orderBy('name')
    .get()
    .then(qs => { 
      qs.forEach( doc => { 
        const data = doc.data()
        const event = {
          'id':doc.id
          , 'name': data.name
          , 'state': data.state
        }
        this.events.push( event )
      } ) 
    } )
  }
}
</script>
