<template>
  <div id="edit-event" class="row justify-content-md-center mt-5 mb-5">
    <div class="col-sm-6">
      <h1>Edit event</h1>
      <form @submit.prevent="updateevent">
        <div class="card mt-4 mb-4">
          <div class="card-header">
            <h4>State</h4>
          </div>
          <div class="form-group pt-4 pl-4">
            <input 
               type="radio" 
               id="publicState"
               value="public"
               v-model="state"
               >
            <label for="publicState">Public</label>
            &nbsp;
            <input 
               type="radio" 
               id="draftState"
               value="draft"
               v-model="state"
               >
            <label for="draftState">Draft</label>
          </div>
        </div>
        <div class="form-group">
          <label>Name</label>
          <input 
            type="text" 
            name="name"
            class="form-control" 
            v-model="name" 
            v-validate="'required'"
            data-vv-validate-on="change|load"
            required
            />
        </div>
        <div class="form-group">
          <label>Description</label>
          <textarea 
            type="text" 
            name="description"
            class="form-control" 
            v-model="description" 
            v-validate="'required'"
            data-vv-validate-on="change|load"
            required
            ></textarea>
        </div>
        <div class="form-group">
          <label>Date</label>
          <input 
            type="text" 
            name="date"
            class="form-control" 
            v-model="date" 
            v-validate="'required'"
            data-vv-validate-on="change|load"
            required
            />
        </div>
        <div class="form-group">
          <label>Gig Length</label>
          <input 
            type="text" 
            name="gigLength"
            class="form-control" 
            v-model="gigLength" 
            />
        </div>
        <div class="card mt-1">
          <div class="card-header">
            <h4>Upload Images</h4>
            <button class="btn btn-primary" @click.prevent="onPickFile">Upload image</button>
            <input 
              type="file" 
              style="display:none" 
              ref="fileInput" 
              accept="image/*"
              @change="onFileChange" 
              multiple
            >
            <p class="card-text">
              <small class="muted-text">Only use .jpg files</small>
              <small>Images may take some time to render when first uploaded. Try reloading if you experience problems</small>
            </p>
          </div>
          <div 
             v-for="imageUrl in imageUrls"
             v-if="imageUrls" 
            >
              <img 
                :src="imageUrl" 
                height="100" 
                width="auto" 
                alt="uploaded image"
                />
            </div>
          <div class="row p-5 justify-content-md-center">
            <div 
               v-for="image in gallery"
               v-if="gallery" 
              >
              <div class="card mr-2 mb-2" >
                <img 
                  :src="'https://firebasestorage.googleapis.com/v0/b/fat-tuesday.appspot.com/o/uploads-output%2Fresized-thumb-' + image + '?alt=media'" 
                  height="100"
                  width="100"
                  class="card-image-top"
                  alt="uploaded image"
                  />
              </div>
            </div>
          </div>
        </div>
        <div class="mt-3">
          <button class="btn btn-lg btn-block btn-primary pr-sm">Submit</button>
          <router-link to="/" class="btn btn-lg btn-block btn-secondary">Cancel</router-link>
        </div>
      </form>
    </div>
  </div>
</template>
<script>

import firebase from 'firebase/app'
import db from '../lib/firebaseInit'
export default {
  name:'edit-event'
  , data() {
    return {
      event_id: ''
      , name: ''
      , date: ''
      , gigLength: ''
      , description: ''
      , images: []
      , imageUrls: []
      , gallery: []
      , state: ''
    }
  },
  created() {
    db.collection('events')
    .doc( this.$route.params.event_id )
    .onSnapshot( doc => {
      const data = doc.data()
      let updatedLocal = ''
      if( data.updatedAt ) updatedLocal = new Date( data.updatedAt ).toLocaleString("en-GB") 
      this.event_id = doc.id
      this.name = data.name
      this.date = data.date
      this.gigLength = data.gigLength
      this.description = data.description
      this.createdAt = data.createdAt
      this.gallery = data.gallery || []
      this.state = data.state
      this.updatedAt = updatedLocal
    } )
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    onFileChange ( e ) {
      const files = e.target.files
      for ( var i=0;i<files.length;i++ ) {
        const fileReader = new FileReader()
        let filename = files[i].name
        if ( filename.lastIndexOf('.')  <=0 ) return alert( "please use a valid file" )
        fileReader.addEventListener( 'load', () => {
          this.imageUrls.push(fileReader.result) 
        } )
        fileReader.readAsDataURL( files[i] )
        this.images.push( files[i] )
      }
    },
    onDeleteImage ( url ) {
      this.gallery.splice(this.gallery.indexOf(url), 1)
      db.collection('events')
      .doc( this.$route.params.event_id )
      .update( {
        gallery: this.gallery
      } )
      .then( () => {
        const storage = firebase.storage()
        storage.ref('uploads/' + url)
        .delete()
        .then(() => {
          return
        })
        .catch( error => alert( error ) )
      } )
      .catch( error => alert( error ) )

    },
    onPickFile () {
      this.$refs.fileInput.click()
    }, 
    fetchData() {
      db.collection('events')
      .doc( this.$route.params.event_id )
      .onSnapshot( doc => {
        const data = doc.data()
        const createdLocal = new Date( data.createdAt ).toLocaleString("en-GB") 
        let updatedLocal = ''
        if( data.updatedAt ) updatedLocal = new Date( data.updatedAt ).toLocaleString("en-GB") 
        this.event_id = doc.id
        this.name = data.name
        this.date = data.date
        this.gigLength = data.gigLength
        this.description = data.description
        this.gallery = data.gallery 
        this.state = data.state
        this.createdAt = createdLocal
        this.updatedAt = updatedLocal
      } )
    },
    updateevent() {
      let updatedAt = new Date()
        , event_id = this.$route.params.event_id
        , ref = db.collection('events').doc( event_id )
      this.$validator.validate()
      .then( result => {
        if(!result) {
          return alert('Please fill in the form as required')
        }
        ref.update( {
          name: this.name
          , date: new Date(this.date)
          , gigLength: this.gigLength
          , description: this.description
          , state: this.state
          , updatedAt: updatedAt
        } )
        .then( () => {
          const storage = firebase.storage()
          let filenames = []
            , i = 0
          this.images.forEach( image => {
            let filename = event_id
              , ext = image.type.split('/')[1]
            i++
            filename += '-'
            filename += Date.now()
            filename += '-'
            filename += i
            filename += '.' + ext
            filenames.push( filename )
            storage.ref('uploads/' + filename).put(image)
          } )
          return Promise.all( filenames )
        } )
        .then( fileData => {
          var combined = [ ...this.gallery, ...fileData ]
          return ref.update( {
            gallery: combined
          } )
        } )
        .then( () => {
          this.$router.push( { 
            name: 'view-event'
            , params: event_id 
          } )
        } )
        .catch( error => alert( error ) )
      } )
    }
  }
}
</script>
