<template>
  <div id="view-venue" class="row justify-content-md-center mt-5">
    <div class="col-sm-6">
      <h1>Venue Details</h1>
      <div class="mb-3">
        <router-link 
          to="/venues/list" 
          class="btn btn-secondary ml-1">
          Back to Venues
        </router-link>
        <router-link 
          to="/venues/new" 
          class="btn btn-secondary ml-1">
          <i class="fa fa-plus"></i>
        </router-link>
      </div>
      <div class="card pt-0">
        <h3 class="card-header">
          {{ name }}
        </h3>
        <div v-if="state" class="card-text p-3">
          <div class="badge badge-secondary mr-1">
            <i class="fas fa-eye"></i>
            {{ state }}
          </div>
        </div>
        <div v-if="gallery" id="galleryCarousel" class="carousel slide" data-ride="carousel">
          <div class="carousel-inner">
            <div v-for="image in gallery" class="carousel-item active">
              <img 
                class="d-block w-100" 
                :src="'https://firebasestorage.googleapis.com/v0/b/fat-tuesday.appspot.com/o/uploads-output%2Fresized-large-' + image + '?alt=media'" 
                alt="First slide">
            </div>
          </div>
        </div>
        <div class="card-body">
          <h5 class="first">
            Slug
          </h5>
          <div class="card-text">
            {{ slug }}
          </div>
          <h5 class="card-text">
            Address
          </h5>
          <div class="card-text">
            {{ address }}
          </div>
          <h5 class="card-text">
            Description
          </h5>
          <div class="card-text">
            {{ description }}
          </div>
          <h5 class='card-text'>
            Lat/long
          </h5>
          <div class="card-text">
            {{ latitude }} {{ longitude }}
          </div>
        </div>
        <div class="card-footer">
          <div v-if="createdAt != 'Invalid Date'" class="card-muted">Created: {{ createdAt }}</div>
          <div v-if="updatedAt != ''" class="card-muted">Last updated: {{ updatedAt }}</div>
        </div>
      </div>
      <div class="mt-3">
        <router-link v-bind:to="{ name:'edit-venue', params: venue_id }" class="btn btn-lg btn-block btn-outline-secondary">Edit</router-link>
        <button v-on:click="deleteVenue" class="btn btn-lg btn-block btn-danger">Delete</button>
      </div>
  </div>
</div>
</template>

<script>
import firebase from 'firebase/app'
import db from '../lib/firebaseInit'
export default {
  name:'view-venue'
  , data() {
    return {
      venue_id: ''
      , name: ''
      , address: ''
      , description: ''
      , latitude: ''
      , longitude: ''
      , slug: ''
      , createdAt: ''
      , updatedAt: ''
      , state: ''
      , gallery: ''
    }
  },
  created() {
    db.collection('venues')
    .doc( this.$route.params.venue_id )
    .onSnapshot( doc => {
      const data = doc.data()
      const createdLocal = new Date( data.createdAt ).toLocaleString("en-GB") 
      let updatedLocal = ''
      if( data.updatedAt ) updatedLocal = new Date( data.updatedAt ).toLocaleString("en-GB") 
      this.venue_id = doc.id
      this.name = data.name
      this.address = data.address
      this.description = data.description
      this.latitude = data.latitude
      this.longitude = data.longitude
      this.slug = data.slug
      this.state = data.state
      this.gallery = data.gallery
      this.createdAt = createdLocal
      this.updatedAt = updatedLocal
    } )
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData() {
      db.collection('venues')
      .doc( this.$route.params.venue_id )
      .onSnapshot( doc => {
        const data = doc.data()
        const createdLocal = new Date( data.createdAt ).toLocaleString("en-GB") 
        let updatedLocal = ''
        if( data.updatedAt ) updatedLocal = new Date( data.updatedAt ).toLocaleString("en-GB") 
        this.venue_id = doc.id
        this.name = data.name
        this.address = data.address
        this.description = data.description
        this.latitude = data.latitude
        this.longitude = data.longitude
        this.slug = data.slug
        this.state = data.state
        this.createdAt = createdLocal
        this.updatedAt = updatedLocal
      } )
    },
    deleteVenue() {
      if( confirm( 'Are you sure' ) ) {
        db.collection('venues')
        .doc( this.$route.params.venues_id )
        .onSnapshot( doc => {
          doc.ref.delete()
          this.$router.push('/venues/list')
        } )
      }
    }
  }
}
</script>
<style scoped>
h5 {
  margin-top:2rem;
  padding-top:1rem;
  border-top: thin #eee solid;
}
.first {
  margin-top:0;
  padding-top:0;
  border-top:none
}
.list-group {
  margin-bottom:2rem
}
</style>
