import Vue from 'vue'
import Router from 'vue-router'
import Landing from '@/components/Landing'
import Dashboard from '@/components/Dashboard'
import EditAct from '@/components/EditAct'
import NewAct from '@/components/NewAct'
import ViewAct from '@/components/ViewAct'
import ListActs from '@/components/ListActs'
import ListVenues from '@/components/ListVenues'
import NewVenue from '@/components/NewVenue'
import ViewVenue from '@/components/ViewVenue'
import EditVenue from '@/components/EditVenue'
import NewEvent from '@/components/NewEvent'
import ListEvents from '@/components/ListEvents'
import EditEvent from '@/components/EditEvent'
import ViewEvent from '@/components/ViewEvent'
import Login from '@/components/Login'
import Register from '@/components/Register'
import firebase from 'firebase/app'
import 'firebase/auth'
import db from '../lib/firebaseInit'

Vue.use( Router )

let router = new Router( {
  routes: [ 
    {
      path: '/'
      , name: 'landing'
      , component: Landing
      , meta: {
        requiresAuth: true
      }
    },
    {
      path: '/dashboard'
      , name: 'dashboard'
      , component: Dashboard
      , meta: {
        requiresAdmin: true
      }
    },
    {
      path: '/login'
      , name: 'login'
      , component: Login
      , meta: {
        requiresGuest: true
      }
    },
    {
      path: '/register'
      , name: 'register'
      , component: Register
      , meta: {
        requiresGuest: true
      }
    },
    {
      path: '/acts/new'
      , name: 'new-act'
      , component: NewAct
      , meta: {
        requiresAdmin: true
      }
    },
    {
      path: '/acts/list'
      , name: 'list-acts'
      , component: ListActs
      , meta: {
        requiresAdmin: true
      }
    },
    {
      path: '/acts/edit/:act_id'
      , name: 'edit-act'
      , component: EditAct
      , meta: {
        requiresAdmin: true
      }
    },
    {
      path: '/acts/:act_id'
      , name: 'view-act'
      , component: ViewAct
      , meta: {
        requiresAdmin: true
      }
    },
    {
      path: '/venues/new'
      , name: 'new-venue'
      , component: NewVenue
      , meta: {
        requiresAdmin: true
      }
    },
    {
      path: '/venues/list'
      , name: 'list-venues'
      , component: ListVenues
      , meta: {
        requiresAdmin: true
      }
    },
    {
      path: '/venues/:venue_id'
      , name: 'view-venue'
      , component: ViewVenue
      , meta: {
        requiresAdmin: true
      }
    },
    {
      path: '/venues/edit/:venue_id'
      , name: 'edit-venue'
      , component: EditVenue
      , meta: {
        requiresAdmin: true
      }
    },
    {
      path: '/events/new'
      , name: 'new-event'
      , component: NewEvent
      , meta: {
        requiresAdmin: true
      }
    },
    {
      path: '/events/list'
      , name: 'list-events'
      , component: ListEvents
      , meta: {
        requiresAdmin: true
      }
    },
    {
      path: '/events/edit/:event_id'
      , name: 'edit-event'
      , component: EditEvent
      , meta: {
        requiresAdmin: true
      }
    },
    {
      path: '/events/:event_id'
      , name: 'view-event'
      , component: ViewEvent
      , meta: {
        requiresAdmin: true
      }
    }
  ]
} )

router.beforeEach((to, from, next) => {
  if( to.matched.some( record => record.meta.requiresAuth ) ) {
    if(!firebase.auth().currentUser) next( { 
      path:'/login' 
      , query: {
        redirect: to.fullPath
      }
    } )

    else next()
  } else if( to.matched.some( record => record.meta.requiresGuest ) ) {
    if(firebase.auth().currentUser) next( { 
      path:'/' 
      , query: {
        redirect: to.fullPath
      }
    } )
    else next()
  } else if( to.matched.some( record => record.meta.requiresAdmin ) ) {
    var userEmail = firebase.auth().currentUser.email
    db.collection('users')
    .doc( userEmail )
    .onSnapshot( doc => {
        const data = doc.data()
        if( data.access != 'admin' ) next( {
            path:'/'
            , query: {
              redirect: to.fullPath
            }
          } )
        else next()
      } )
  } else {
    next()
  }
})

export default router
