<template>
  <div id="new-venue" class="row justify-content-md-center mt-5 mb-5">
    <div class="col-sm-6">
      <h1>New venue</h1>
      <form @submit.prvenue="saveVenue">
        <div class="card mt-4 mb-4">
          <div class="card-header">
            <h4>State</h4>
          </div>
          <div class="form-group pt-4 pl-4">
            <input 
               type="radio" 
               id="publicState"
               value="public"
               v-model="state"
               >
            <label class="ml-1" for="publicState">Public</label>
            &nbsp;
            <input 
               type="radio" 
               id="draftState"
               value="draft"
               v-model="state"
               >
            <label class="ml-1" for="draftState">Draft</label>
          </div>
        </div>
        <div class="form-group">
          <label>Name</label>
          <input 
            type="text" 
            name="name"
            class="form-control" 
            v-model="name" 
            v-validate="'required'"
            data-vv-validate-on="change|load"
            required
            />
        </div>
        <div class="form-group">
          <label>Address</label>
          <input 
            type="text" 
            name="address"
            class="form-control" 
            v-model="address" 
            v-validate="'required'"
            data-vv-validate-on="change|load"
            required
            />
        </div>
        <div class="form-group">
          <label>Description</label>
          <input 
            type="text" 
            name="description"
            class="form-control" 
            v-model="description" 
            v-validate="'required'"
            data-vv-validate-on="change|load"
            required
            />
        </div>
        <div class="form-group">
          <label>Latitude</label>
          <input 
            type="text" 
            name="latitude"
            class="form-control" 
            v-model="latitude" 
            v-validate="'required'"
            data-vv-validate-on="change|load"
            required
            />
        </div>
        <div class="form-group">
          <label>Longitude</label>
          <input 
            type="text" 
            name="longitude"
            class="form-control" 
            v-model="longitude" 
            v-validate="'required'"
            data-vv-validate-on="change|load"
            required
            />
        </div>
        <div class="form-group">
          <label>slug</label>
          <input 
            type="text" 
            name="slug"
            class="form-control" 
            v-model="slug" 
            v-validate="'required'"
            data-vv-validate-on="change|load"
            required
            />
        </div>
        <div class="mt-3">
          <button id="submit" class="btn btn-lg btn-block btn-primary pr-sm">Submit</button>
          <router-link to="/" class="btn btn-lg btn-block btn-secondary">Cancel</router-link>
        </div>
       </form>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase/app'
import db from '../lib/firebaseInit'
export default {
  name:'new-venue'
  , data() {
    return {
      venue_id: ''
      , name: ''
      , address: ''
      , description: ''
      , latitude: ''
      , longitude: ''
      , slug: '' 
      , state: ''
      , upload: ''
      , images: []
      , imageUrls: []
      , gallery: []
    }
  },
  methods: {
    onFileChange ( e ) {
      const files = e.target.files
      for ( var i=0;i<files.length;i++ ) {
        const fileReader = new FileReader()
        let filename = files[i].name
        if ( filename.lastIndexOf('.')  <=0 ) return alert( "please use a valid file" )
        fileReader.addvenueListener( 'load', () => {
          this.imageUrls.push(fileReader.result) 
        } )
        fileReader.readAsDataURL( files[i] )
        this.images.push( files[i] )
      }
    },
    onPickFile () {
      this.$refs.fileInput.click()
    }, 
    saveVenue() {
      document.getElementById('submit').setAttribute('disabled', 'disabled')
      const currentUser = firebase.auth().currentUser.email
      const createdAt = new Date()
      let ref = ''
      this.$validator.validate()
      .then( result => {
        if(!result) {
          return alert('Please fill in the form as required')
        }
        db.collection('venues')
        .add( {
          name: this.name
          , address: this.address
          , description: this.description
          , latitude: this.latitude
          , longitude: this.Longitude
          , slug: this.slug
          , state: this.state
        } )
        .then( docRef => {
          const storage = firebase.storage()
          let filenames = []
            , i = 0
          ref = docRef
          this.images.forEach( image => {
            let filename = docRef.id
              , ext = image.type.split('/')[1]
            i++
            filename += '-'
            filename += Date.now()
            filename += '-'
            filename += i
            filename += '.' + ext
            filenames.push( filename )
            storage.ref('uploads/' + filename).put(image)
          } )
          return Promise.all( filenames )
        })
        .then( fileData => {
          return ref.update( {
            gallery: fileData
          } )
        } )
        .then( docRef => this.$router.push('/dashboard'))
        .catch( error => alert( error ) )
      } )
    }
  }
}
</script>
