<template>
  <div id="list-acts" class="row justify-content-md-center mt-5 mb-5">
    <div class="col-sm-6">
      <h1>List All Acts</h1>
      <div class="mb-3">
        <router-link 
          to="/acts/new" 
          class="btn btn-secondary ml-1">
          <i class="fa fa-plus"></i>
        </router-link>
      </div>
      <div class="list-group">
        <router-link 
          v-for="act in acts" 
          v-bind:key="act.id" 
          v-bind:to="act.id" 
          class="list-group-item list-group-item-action"
          >
          <div class="badge badge-secondary">
            {{ act.state }}
          </div>
          &nbsp;
          <div class="badge badge-secondary">
            {{ act.createdLocal }}
          </div>
          &nbsp;
          <div v-if="act.applicationType" class="badge badge-primary">
            {{ act.applicationType }}
            &nbsp;
          </div>
          <div v-if="act.updatedAt" class="badge badge-primary">
            updated: {{ act.updatedLocal }}
            &nbsp;
          </div>
          {{ act.name }}
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import db from '../lib/firebaseInit'
export default {
  name:'list-acts'
  , data() {
    return {
      updatedAt:''
      , createdAt: ''
      , acts: []
      , applicationType: ''
      , state: ''
    }
  }, 
  created() {
    db.collection('acts')
    .orderBy('act_name')
    .get()
    .then(qs => { 
      qs.forEach( doc => { 
        const data = doc.data()
        const act = {
          'id':doc.id
          , 'name': data.act_name
          , 'applicationType': data.applicationType
          , 'state': data.state
        }
        act.createdLocal = new Date( data.createdAt ).toDateString() 
        act.updatedLocal = data.updatedAt 
          ? new Date( data.updatedAt ).toLocaleString("en-GB") 
          : null
        this.acts.push( act )
      } ) 
    } )
  }
}
</script>
