<template>
  <div id="new-event" class="row justify-content-md-center mt-5 mb-5">
    <div class="col-sm-6">
      <h1>New Event</h1>
      <form @submit.prevent="saveevent">
        <div class="form-group">
          <label>Name</label>
          <input 
            type="text" 
            name="name"
            class="form-control" 
            v-model="name" 
            v-validate="'required'"
            data-vv-validate-on="change|load"
            required
            />
        </div>
        <div class="form-group">
          <label>Description</label>
          <input 
            type="text" 
            name="description"
            class="form-control" 
            v-model="description" 
            v-validate="'required'"
            data-vv-validate-on="change|load"
            required
            />
        </div>
        <div class="form-group">
          <label>Date</label>
          <input 
            type="text" 
            name="date"
            class="form-control" 
            v-model="date" 
            v-validate="'required'"
            data-vv-validate-on="change|load"
            required
            />
        </div>
        <div class="form-group">
          <label>Gig Length</label>
          <input 
            type="text" 
            name="gigLength"
            class="form-control" 
            v-model="gigLength" 
            />
        </div>
        <div class="card mt-1">
          <div class="card-header">
            <h4>Upload Images</h4>
            <button class="btn btn-primary" @click.prevent="onPickFile">Upload image</button>
            <input 
              type="file" 
              style="display:none" 
              ref="fileInput" 
              accept="image/*"
              @change="onFileChange" 
              multiple
            >
            <p class="card-text">
              <small class="muted-text">Only use .jpg files</small>
              <small>Images may take some time to render when first uploaded. Try reloading if you experience problems</small>
            </p>
          </div>
          <div 
             v-for="imageUrl in imageUrls"
             v-if="imageUrls" 
            >
              <img 
                :src="imageUrl" 
                height="150" 
                width="auto" 
                alt="uploaded image"
                />
            </div>
        </div>
        <div class="mt-3">
          <button id="submit" class="btn btn-lg btn-block btn-primary pr-sm">Submit</button>
          <router-link to="/" class="btn btn-lg btn-block btn-secondary">Cancel</router-link>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase/app'
import db from '../lib/firebaseInit'
export default {
  name:'new-event'
  , data() {
    return {
      event_id: ''
      , name: ''
      , date: ''
      , gigLength: ''
      , description: ''
      , upload: ''
      , images: []
      , imageUrls: []
    }
  },
  methods: {
    onFileChange ( e ) {
      const files = e.target.files
      for ( var i=0;i<files.length;i++ ) {
        const fileReader = new FileReader()
        let filename = files[i].name
        if ( filename.lastIndexOf('.')  <=0 ) return alert( "please use a valid file" )
        fileReader.addEventListener( 'load', () => {
          this.imageUrls.push(fileReader.result) 
        } )
        fileReader.readAsDataURL( files[i] )
        this.images.push( files[i] )
      }
    },
    onPickFile () {
      this.$refs.fileInput.click()
    }, 
    saveevent() {
      document.getElementById('submit').setAttribute('disabled', 'disabled')
      const currentUser = firebase.auth().currentUser.email
      const createdAt = new Date()
      let ref = ''
      this.$validator.validate()
      .then( result => {
        if(!result) {
          return alert('Please fill in the form as required')
        }
        db.collection('events')
        .add( {
          name: this.name
          , date: new Date(this.date)
          , gigLength: this.gigLength
          , description: this.description
          , createdAt: createdAt
        } )
        .then( docRef => {
          const storage = firebase.storage()
          let filenames = []
            , i = 0
          ref = docRef
          this.images.forEach( image => {
            let filename = docRef.id
              , ext = image.type.split('/')[1]
            i++
            filename += '-'
            filename += Date.now()
            filename += '-'
            filename += i
            filename += '.' + ext
            filenames.push( filename )
            storage.ref('uploads/' + filename).put(image)
          } )
          return Promise.all( filenames )
        })
        .then( fileData => {
          return ref.update( {
            gallery: fileData
          } )
        } )
        .then( docRef => this.$router.push('/dashboard'))
        .catch( error => alert( error ) )
      } )
    }
  }
}
</script>
