<template>
  <div id="list-venues" class="row justify-content-md-center mt-5 mb-5">
    <div class="col-sm-6">
      <h1>List All Venues</h1>
      <div class="mb-3">
        <router-link 
          to="/venues/new" 
          class="btn btn-secondary ml-1">
          <i class="fa fa-plus"></i>
        </router-link>
      </div>
      <div class="list-group">
        <router-link 
          v-for="venue in venues" 
          v-bind:key="venue.id" 
          v-bind:to="venue.id" 
          class="list-group-item list-group-item-action"
          >
          <div v-if="venue.state" class="badge badge-secondary">
            {{ venue.state }}
          </div>
          &nbsp;
          {{ venue.name }}
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import db from '../lib/firebaseInit'
export default {
  name:'list-venues'
  , data() {
    return {
      updatedAt:''
      , createdAt: ''
      , venues: []
      , applicationType: ''
      , state: ''
    }
  }, 
  created() {
    db.collection('venues')
    .orderBy('name')
    .get()
    .then(qs => { 
      qs.forEach( doc => { 
        const data = doc.data()
        const venue = {
          'id':doc.id
          , 'name': data.name
          , 'address': data.address
          , 'state': data.state
        }
        this.venues.push( venue )
      } ) 
    } )
  }
}
</script>
