<template>
  <div id="view-event" class="row justify-content-md-center mt-5">
    <div class="col-sm-6">
      <h1>Event Details</h1>
      <div class="mb-3">
        <router-link 
          to="/events/list" 
          class="btn btn-secondary ml-1">
          Back to Events
        </router-link>
        <router-link 
          to="/events/new" 
          class="btn btn-secondary ml-1">
          <i class="fa fa-plus"></i>
        </router-link>
      </div>
      <div class="card pt-0">
        <h3 class="card-header">
          {{ name }}
        </h3>
        <div v-if="state" class="card-text p-3">
          <div class="badge badge-secondary mr-1">
            <i class="fas fa-eye"></i>
            {{ state }}
          </div>
        </div>
        <div v-if="gallery" id="galleryCarousel" class="carousel slide" data-ride="carousel">
          <div class="carousel-inner">
            <div v-for="image in gallery" class="carousel-item active">
              <img 
                class="d-block w-100" 
                :src="'https://firebasestorage.googleapis.com/v0/b/fat-tuesday.appspot.com/o/uploads-output%2Fresized-large-' + image + '?alt=media'" 
                alt="First slide">
            </div>
          </div>
        </div>
        <div class="card-body">
          <h5 class="first">
            Description
          </h5>
          <div class="card-text">
            {{ description }}
          </div>
          <h5 class="card-text">
            Gig Length
          </h5>
          <div class="card-text">
            {{ gigLength }}
          </div>
          <h5 class="card-text">
            Date
          </h5>
          <div class="card-text">
            {{ date }}
          </div>
        </div>
        <div class="card-footer">
          <div v-if="createdAt != 'Invalid Date'" class="card-muted">Created: {{ createdAt }}</div>
          <div v-if="updatedAt != ''" class="card-muted">Last updated: {{ updatedAt }}</div>
        </div>
      </div>
      <div class="mt-3">
        <router-link v-bind:to="{ name:'edit-event', params: event_id }" class="btn btn-lg btn-block btn-outline-secondary">Edit</router-link>
        <button v-on:click="deleteevent" class="btn btn-lg btn-block btn-danger">Delete</button>
      </div>
  </div>
</div>
</template>
<script>

import firebase from 'firebase/app'
import db from '../lib/firebaseInit'
export default {
  name:'view-event'
  , data() {
    return {
      event_id: ''
      , name: ''
      , description: ''
      , gigLength: ''
      , date: ''
      , createdAt: ''
      , updatedAt: ''
      , state: ''
      , gallery: ''
    }
  },
  created() {
    db.collection('events')
    .doc( this.$route.params.event_id )
    .onSnapshot( doc => {
      const data = doc.data()
      const createdLocal = new Date( data.createdAt ).toLocaleString("en-GB") 
      let updatedLocal = ''
      if( data.updatedAt ) updatedLocal = new Date( data.updatedAt ).toLocaleString("en-GB") 
      this.event_id = doc.id
      this.name = data.name
      this.description = data.description
      this.gigLength = data.gigLength
      this.date = data.date
      this.state = data.state
      this.gallery = data.gallery
      this.createdAt = createdLocal
      this.updatedAt = updatedLocal
    } )
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData() {
      db.collection('events')
      .doc( this.$route.params.event_id )
      .onSnapshot( doc => {
        const data = doc.data()
        const createdLocal = new Date( data.createdAt ).toLocaleString("en-GB") 
        let updatedLocal = ''
        if( data.updatedAt ) updatedLocal = new Date( data.updatedAt ).toLocaleString("en-GB") 
        this.event_id = doc.id
        this.name = data.name
        this.description = data.description
        this.gigLength = data.gigLength
        this.date = data.date
        this.state = data.state
        this.createdAt = createdLocal
        this.updatedAt = updatedLocal
      } )
    },
    deleteevent() {
      if( confirm( 'Are you sure' ) ) {
        db.collection('events')
        .doc( this.$route.params.events_id )
        .onSnapshot( doc => {
          doc.ref.delete()
          this.$router.push('/events/list')
        } )
      }
    }
  }
}
</script>
<style scoped>
h5 {
  margin-top:2rem;
  padding-top:1rem;
  border-top: thin #eee solid;
}
.first {
  margin-top:0;
  padding-top:0;
  border-top:none
}
.list-group {
  margin-bottom:2rem
}
</style>
